<template>
    <v-container id="template" >
        <v-btn id="print" class="blue white--text" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="ml-4 red white--text" to="/pembukuan" link>Back</v-btn>
       
       <template id="template">
           <h2 class="text-center">Report Jurnal</h2><br>
            <table id="table" >
                <thead>
                    <tr style="font-size: 9pt">
                        <th id="th">Tanggal</th>
                        <th id="th">No Bukti</th>
                        <th id="th">Keterangan</th>
                        <th id="th">Kodeper</th>
                        <th id="th">Kode Lawan</th>
                        <th id="th">Debet</th>
                        <th id="th">Kredit</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in jurnals" :key='item.id' style="font-size: 9pt">
                        <td id="td" width="75.5px" class="text-center">{{item["tanggal"]}}</td>
                        <td id="td" width="56.6px" class="text-center" v-if="item.nobukti == 'null'"></td>
                        <td id="td" width="56.6px" class="text-center" v-else>{{item["nobukti"]}}</td>
                        <td id="td" width="233px">{{item["keterangan"]}}</td>
                        <td id="td" width="90px" class="text-center">{{ item["kodedebet"]}}</td>
                        <td id="td" width="75px" class="text-center">{{ item["kodekredit"]}}</td>                        
                        <td id="td" width="112px" class="text-right">Rp {{item["debet"] | currency}}</td>
                        <td id="td" width="112px" class="text-right">Rp {{item["kredit"] | currency }}</td>
                    </tr>
                    <tr style="font-size: 9pt">
                        <td colspan="5" id="td" class="text-right"><b>Total</b></td>
                        <td class="text-right" id="td">Rp {{totaldebet | currency}}</td>
                        <td class="text-right" id="td">Rp {{totalkredit | currency}}</td>

                        <!-- <td  id="td" colspan="2" class="text-right"><pre><b>{{this.totalpembelians}}</b></pre></td> -->
                    </tr>
                </tbody>
            </table>
       </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                keyword: '',
                load: false,
                headers: [
                    {
                        text: 'Tanggal',
                        value: 'Tanggal'
                    },
                    {
                        text: 'No Bukti',
                        value: 'No Bukti'
                    },
                    {
                        text: 'Keterangan',
                        value: 'Keterangan'
                    },
                    {
                        text: 'Kode Perkiraan',
                        value: 'Kode Perkiraan'
                    },
                    {
                        text: 'Debet',
                        value: 'Debet'
                    },
                    {
                        text: 'Kredit',
                        value: 'Kredit'
                    },
                    // {
                    //     text: 'Sub Total',
                    //     value: 'Sub Total'
                    // },
                    // {
                    //     text: 'Total',
                    //     value: 'Total'
                    // },
                ],
                jurnals: [],
                totalkredit: 0,
                totaldebet: 0,
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'bukujurnal/reportjurnal'
                this.$http.get(uri).then(response => {
                    this.jurnals = response.data
                    // this.totalpembelians = response.data.total[0].TotalAll
                    for (var x in this.jurnals) {
                        this.totaldebet += this.jurnals[x].debet
                        this.totalkredit += this.jurnals[x].kredit
                    }
                })
            },

            
            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style>
    @media print{
        #print{
            display: none;
        }
        #template{
            background-color: white
        }
    }
    
    #table, #th, #td {
        border: 1.5px solid black;
        border-collapse: collapse;
    }    
</style>